@import url('https://fonts.googleapis.com/css2?family=PT+Sans+Narrow:wght@400;700&family=Play&display=swap');

.row-1{
    margin-top: 4vh;
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    margin-bottom: 3vh;
}

.column{
    margin: 0 3vw;
    display: flex;
    flex-direction: column;
   
}
.column h4{
    font-family: 'Play', sans-serif;
   
}
.column a{
    text-decoration: none;
    color: var(--clr1) !important;
    font-family: 'PT Sans Narrow', sans-serif;
    cursor: pointer;
    
}
.column a:hover{
    color: var(--clr2) !important;

}
.Bottom{
    margin-bottom: 3vh;
    
}
.Bottom p{
    font-size: 0.7rem;
    margin-left: 2vw;
    font-family: 'PT Sans Narrow', sans-serif
    
}
@media screen and (min-aspect-ratio: 16/9) {
    .column h4{
       font-size: 2.3rem;
       
    }
    .column a{
        font-size: 1.5rem;
    }
}
@media (max-width:600px)  {

    .row-1{
        display: block;
    }
    .Bottom{
        width: 95%;
    }
    .Bottom p{
        font-size: 0.6rem;
        
        
    }
}
