@import url('https://fonts.googleapis.com/css2?family=Play&family=Zen+Dots&display=swap');
.Brand-container{
    margin-top: 1vh;
    display: flex;
    margin-right: auto;
    margin-left: auto;
    position: relative;
    
    flex-direction: column;
    
}
.heading{
    color: var(--clr1);
    display: flex;
    margin-left: 1.5vw;
    align-items: center;
    text-align: center;
    clear: both;
    font-family: 'Play', sans-serif;
    
}
.Big-text{
    color: var(--clr1);
    font-size: 3vw;
    font-weight: bolder;
    text-decoration: none;
    text-align: center;
    font-family: 'Play', sans-serif;
}
.Small-text{
    color: var(--clr1);
    font-size: 2.5vw;
    font-weight: 550;
    text-decoration: none;
    margin-left: 3vw;
    text-align: center;
    
}
.BrandsItem{
    margin-left: 3rem;
}
.BrandsItem:first-child{
    margin-left: 0;
}

@media screen and (min-aspect-ratio: 16/9) {
    .BrandsItem{
        margin-left: 6rem;
    }
    .BrandsItem:first-child{
        margin-left: 0;
    }
}
@media (max-width:801px)  {
    .BrandsItem{
        margin-left: 1rem;
    }
}

