.card{
    display: flex;
    border-radius: 3%;
    flex-direction: column;
    align-items: center;
    
    /* margin-left: 50px; */
    width: 20vw;
    height: 50vh;
    text-decoration: none;
    background-color: var(--clr1);
    border-radius: 1.3rem 0 1.3rem 0;
}

.card img{

    margin-top: 10px;
    width: 15vw;
    height: 20vh;
}
.card-body{
    width: 17vw;
    height: 10vh;
    display: flex;
    flex-direction: column;
    /* align-items: flex-start; */
    align-items: center;
    padding:  0 18px;
    justify-content: space-between;
    flex: 1 1 auto;
}
.card-body h5{
    margin-top: 1vh;

    color: var(--clr4) !important;
    
    font-weight: 600;
    font-family: 'Orbitron', sans-serif;
    font-weight: 600;
    font-size: 1rem;
    letter-spacing: 1px;
}
.card-text {
    font-family: 'Orbitron', sans-serif;
    font-weight: 800;
    font-size: 2rem;
    letter-spacing: 1px;
    color: var(--clr2) !important;
}
.card-body h4{
    
    color: var(--clr4) !important;
    font-family: 'Orbitron', sans-serif;
    font-weight: 700;
    font-size: 1.2rem;
    letter-spacing: 1px;
}
.count{
    color: var(--clr4) !important;
    font-size: 0.7rem;
}
.btnbottom{
    height: 5vw;
        width: 13vw;
}
