    .buybtn {

        --border-width: 0.5em;
        --edge-size: 1em;
        height: 4vw;
        width: 15vw;
        margin-top: 2vh;
        margin-bottom: 3vh;
        cursor: pointer;
        color: var(--clr1);
        background-color: var(--clr4);
        padding: 0.5em 1.5em;
        border: 0;
        position: relative;
        isolation: isolate;
        display: inline-grid;
        place-content: center;
        clip-path: polygon(
          var(--edge-size) 0%,
          100% 0,
          100% calc(100% - var(--edge-size)),
          calc(100% - var(--edge-size)) 100%,
          0 100%,
          0% var(--edge-size)
        );
        transition: color 250ms;
      }
      
      .buybtn::before {
        content: "";
        position: absolute;
        inset: 0;
        background: var(--border-color);
        z-index: -2;
        box-sizing: border-box;
      }
      
      .buybtn::after {
        content: "";
        position: absolute;
        inset: 0;
        background: var(--background-color);
        z-index: -1;
        clip-path: polygon(
          /* // top left 1 */
          var(--border-width) calc(var(--edge-size) + var(--border-width) * 0.5),
          /* // top left 2 */
          calc(var(--edge-size) + var(--border-width) * 0.5) var(--border-width),
          /* // top right */
          calc(100% - var(--border-width)) var(--border-width),
          /* //bottom right - 1 */
          calc(100% - var(--border-width))
            calc(100% - calc(var(--edge-size) + var(--border-width) * 0.5)),
          /* // bottom right - 2 */
          calc(100% - calc(var(--edge-size) + var(--border-width) * 0.5))
            calc(100% - var(--border-width)),
          /* // bottom left */
          calc(var(--border-width)) calc(100% - var(--border-width))
        );
        transition: clip-path 500ms;
        box-sizing: border-box;
      }
      
      
      
      .buybtn:hover::after {
        /* // clip-path: polygon(50% 50%, 50% 50%, 50% 50%, 50% 50%, 50% 50%, 50% 50%); */
      
        clip-path: polygon(
          /* // top left 1 */
          calc(100% - var(--border-width))
            calc(100% - calc(var(--edge-size) + var(--border-width) * 0.5)),
          /* // top left 2 */
          calc(100% - var(--border-width)) var(--border-width),
          /* // top right */
          calc(100% - var(--border-width)) var(--border-width),
          /* //bottom right - 1 */
          calc(100% - var(--border-width))
            calc(100% - calc(var(--edge-size) + var(--border-width) * 0.5)),
          /* // bottom right - 2 */
          calc(100% - calc(var(--edge-size) + var(--border-width) * 0.5))
            calc(100% - var(--border-width)),
          /* // bottom left */
          calc(100% - calc(var(--edge-size) + var(--border-width) * 0.5))
            calc(100% - var(--border-width))
        );
      }
      .buybtn p{
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: 'Unbounded', cursive;
        font-size: 1.5rem;
        font-weight: bold;
    }
.buybtn:hover {
    background-color: var(--clr2);
    color: var(--clr4);

}

.btn{
    margin-bottom: 2vh;
    border: 1px solid var(--clr1);
    color: var(--clr1);
    background-color: var(--clr4);
}
.btn:hover {
    background-color: var(--clr1);
    color: var(--clr4);
    border: 1px solid var(--clr4);
  }







@media screen and (min-aspect-ratio: 16/9) {
    .card{
        height: 65vh;
        
        
    }
    .card img{
        margin-top: 10px;
        width: 30vh;
        height: 15vw;
    }
    .card-body{
        justify-content: space-between;
    }
    .card-body h5{
        
        font-size: 1.6rem;
        letter-spacing: 1px;
        
    }
    .card-body h4{
        
        font-size: 2rem;
        letter-spacing: 1px;
        
    }
    .count{
        font-size: 1.5rem;
    }
    .card-text{
        
        font-size: 3rem;
        letter-spacing: 1px;
        
    }
    .buybtn{
        width: 12vw;
        height: 4vh;
        font-size: 2rem;
    }
  }
@media (max-width:600px)  {
    .item{
        display: flex;
        justify-content: center;
        height: 50vh;

    }
    .card{
        /* margin-left: 50px; */
        width: 30vw;
        height: 40vh;

        
        
    }
    .card img{
        width: 20vw;
        height: 20vw;
    }
    .card-body h5{
        
        font-size: 2.5vw;
        margin: 0.7vh 0 ;
        
    }
    .card-text {
        font-size: 1.9vw;
        margin-bottom: 0.7vh;
    }
    .card-body h4{
        
        font-size: 2vw;

    }
    .count{
        font-size: 1.2vw;
    }
    .btn{
        height: 3vh;
        font-size: 2vw;
    }
  }
  @media (max-width:801px)  {
