.container1{
  margin-top: 5vh;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 55vh;
  
}
.container1 img{
  width: 60rem;  
  /* 988px */
  height: 50vh;

  border-radius:  0 5rem 0 5rem ;

}
.grey-btn{
display: flex;
justify-content: center;
}
.container1 .btn-circle {
  position: relative;
  width: 1vw;
  height: 1.5vh;
  background-color: #dddddd;
  margin-left: 1vw;
  border-radius: 50rem;
  border: 1px solid transparent;
  bottom: 3.337041156840934vh;
  
}

.container1 .btn-circle:hover {
  background-color: white;
}
  @media screen and (min-aspect-ratio: 16/9) {
    .container1{
      margin-bottom: 1vh;
      height: 70vh;
    }
    .container1 img{
      width: 100rem;  
      /* 988px */
      height: 65vh;
     
 
    
  }
  .container1 .btn-circle {
    width: 1.2vw;
    height: 2.5vh;
    border-radius: 50rem;
  }
  }
  @media (max-width:600px)  {
    .container1 img{
      height: 20vh;
  
  }
  .container1 .btn-circle {
    position: relative;
    width: 3vw;
    height: 2.3vh;
    background-color: #dddddd;
    margin-left: 8vw;
    border-radius: 50%;
    bottom: 3.337041156840934vh;
    
  }
  }
  @media (max-width:801px)  {
    .container1{
      margin-top: 8vh;
      height: 35vh;
    }
    .container1 img{
      height: 30vh;
      width: 90vw;
      border-radius: 5rem;
  }
  .container1 .btn-circle {
    position: relative;
    width: 1.2rem;
    height: 1.2rem;
    background-color: #dddddd;
    margin-left: 4vw;
    border-radius: 50%;
    bottom: 3.337041156840934vh;
    
  }
  }